<template>
   <div>
       <div class="app">
           <!--  头部  -->
           <div class="myHead pr">
               <div class="imgBox">
                   <img v-if="token" @click="goUserInfo" :src="avatar" alt="">
                   <img @click="goLogin" v-else src="@/static/images/my/per_image_default.png" alt="">
                   <span @click="goUserInfo" v-if="token" style="color: #fff;">{{name}}<i style="color: rgb(210, 210, 210);font-size: .12rem">(点击修改)</i></span>
                   <!--  未登录成功时  -->
                   <span @click="goLogin" v-else>登录后体验更多服务</span>
               </div>
           </div>
           <ul class="myList">
               <li><router-link to="/introduction"><i class="pingtai"></i><p>平台简介</p><span></span></router-link></li>
               <li><router-link to="/wcurrency"><i class="zijin"></i><p>个人资金</p><span></span></router-link></li>
               <li><router-link to="/redindex"><i class="hbsy"></i><p>红包总收益</p><span></span></router-link></li>
               <li><router-link to="/treelog"><i class="cfsy"></i><p>财富树总收益</p><span></span></router-link></li>
               <li><router-link to="/backups"><i class="pwd"></i><p>备份账户密码</p><span></span></router-link></li>
               <li><router-link to="/wechatcode"><i class="wxcode"></i><p>上传微信二维码</p><span></span></router-link></li>
               <li><router-link to="/invite"><i class="friend"></i><p>邀请好友</p><span></span></router-link></li>
               <li><router-link to="/opinion"><i class="feedback"></i><p>意见反馈</p><span></span></router-link></li>
               <li><router-link to="/mnotice"><i class="notice"></i><p>公告</p><div v-if="unlook > 0" class="point"></div><span></span></router-link></li>
           </ul>

           <!--  登录成功后状态  -->
           <div class="signOutBox" v-if="token">
               <button @click="logout">退出登录</button>
           </div>
       </div>
       <tab-bar></tab-bar>
   </div>
</template>

<script>
    import tabBar from '@/components/tabBar'
    import { mapGetters } from 'vuex'
    import { lookNotice } from '@/api/user'
    export default {
        components: {
            tabBar
        },
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ])
        },
        name: "Mine",
        data(){
            return{
                defaultAvatr: require("@/static/images/my/per_image_default.png"),
                unlook:0
            }
        },
        methods:{
            // 登录
            goLogin()
            {
               this.$router.push({path:'/login?redirect=mine'})
            },
            // 设置跟人信息
            goUserInfo()
            {
                this.$router.push({path:'/userinfo'})
            },
            // 退出登录
            logout()
            {
                this.$store.dispatch('user/logout').then(() => {
                }).catch(() => {

                })
            },
            // 公告规则查看情况
            getLookNotice()
            {
                if (this.name)
                {
                    lookNotice().then(res => {
                        this.unlook = res.unlook
                    })
                }

            }
        },
        activated(){
            this.getLookNotice()
        }
    }
</script>

<style scoped>
   @import "../../static/css/mine.css";
   .app .myList li a{
       position: relative;
   }
   .app .myList li a .point{
       width: .08rem;
       height: .08rem;
       display: inline-block;
       border-radius: 50%;
       background: #E33C3D;
       position: absolute;
       right: .34rem;
       top: .21rem;
   }
</style>



