<template>

    <div>
        <div class="app">
            <!--  顶部搜索  -->
            <div class="searchBox ov">
                <div class="select fl">
                    <router-link to="/currency">
                        <span>{{currency.name}}</span>
                        <i></i>
                    </router-link>
                </div>
                <div class="search pr fr" @click="goSearch">
                    <!--<i></i>-->
                    <input class="placeholder" type="text" placeholder="请输入红包房间号" readonly>
                </div>
            </div>
            <van-swipe :autoplay="3000" indicator-color="white">
                <van-swipe-item @click="goAdDetail(item)" v-for="(item,index) in adData" :key="index"><img style="display: block;width: 100%;height: auto" :src="item.pic" alt=""></van-swipe-item>
            </van-swipe>

            <!--  列表  -->
            <div class="listBox pr">
                <!--  财富树密码框  -->
                <div class="wealthTree">
                    <i></i>
                    <div class="wealthTreePwd ov"><input class="fl" type="text" placeholder="请输入财富树密码" v-model="treeNumber"><button class="fr" @click="searchTree">确定</button></div>
                </div>
                <div class="usdtTitle ov">
                    <p class="fl">{{currency.name}}红包接龙</p>
                    <router-link :to="{path:'/currencyred', query:{currencyId:currency_id}}" class="fr">更多</router-link>
                </div>
                <ul class="listUl">
                    <li v-for="(item,index) in redList">
                        <router-link :to="{path:'/redchat', query:{groupId:item.id,type:'all',currency:item.currency_id, order:index+1}}">
                            <img :src="item.icon" alt />
                            <span>{{item.name}}</span>
                        </router-link>
                    </li>

                </ul>
                <div class="usdtTitle ov hotTitle">
                    <p class="fl">热门币种区红包接龙</p>
                    <router-link to="/hotred" class="fr">更多</router-link>
                </div>
                <ul class="listUl">
                    <li v-for="(item, index) in currencyData" :key="index">
                        <router-link v-if="item.state == 1"  :to="{path:'/currencyred', query:{currencyId:item.id}}">
                            <img :src="item.icon" alt />
                            <span>{{item.name}}</span>
                        </router-link>
                        <a v-else href="javascript:void (0)" @click="showAlert">
                            <img :src="item.icon" alt="">
                            <span>{{item.name}}</span>
                        </a>
                    </li>

                </ul>
            </div>
        </div>
         <tab-bar></tab-bar>
    </div>

</template>

<script>
    import Cryptojs from 'crypto-js'
    import { currencyList, currencyInfo, homeList, adList, reward } from "@/api/index"
    import { mapGetters } from 'vuex'
    import tabBar from '@/components/tabBar'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import Vue from 'vue';
    import { Swipe, SwipeItem } from 'vant';
    Vue.use(Swipe).use(SwipeItem);
    import 'vant/lib/swipe/style';
    import 'vant/lib/swipe-item/style';
    import Dialog from 'vant/lib/dialog';
    import 'vant/lib/dialog/style';

    export default {
        components: {
            tabBar,
        },
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ])
        },
        name: "Index",
        data()
        {
            return {

                // 为了快速先写成固定模式
                currency:{},
                currency_id:'',
                treeNumber:'',
                adData:[],
                currencyData:[],//币种
                redList:[]//红包群
            }
        },
        mounted()
        {

            this.currency_id = this.$route.query.currencyId
            /** 处理一下 默认先为1 */
            this.currency_id = this.currency_id ?  this.currency_id : 1;

            this.getCurrencyInfo()
            // 获取轮播图
            this.getAdList()
            this.getHomeList()

        },
        methods:{
            // 搜索财富树
            searchTree()
            {
               if (this.treeNumber == '' || this.treeNumber.length == 0)
               {
                   Toast('请输入财富树密码');
                   return false
               }

               this.$router.push({path:'/jointree', query:{number:this.treeNumber}})
            },
            getCurrencyInfo()
            {
                currencyInfo(this.currency_id).then(res => {

                    this.currency = res
                })
            },
            // 获取列表
            getHomeList()
            {
                homeList(this.currency_id).then(res => {

                    this.currencyData = res.currency
                    this.redList = res.redList
                })
            },
            // 轮播图
            getAdList()
            {
                adList().then(res => {

                    this.adData = res
                })
            },
            goSearch()
            {
                this.$router.push('/searchred')
            },
            goAdDetail(row)
            {
                if (row.type == 1)
                {
                    this.$router.push('/adDetail?id='+row.id)

                } else
                {
                    // 项目方接入
                    this.$router.push('/cooperation?id='+row.id)
                }

            },
            getReward()
            {
                reward().then(res => {

                    if (res['reward'] == true)
                    {
                        Dialog.alert({
                            title: '首次登陆',
                            message: res.message
                        }).then(() => {
                            // on close
                        });
                    }
                })
            },
            showAlert()
            {
                Dialog.alert({
                    title: '系统提醒',
                    message: '当前币种暂未开放'
                }).then(() => {
                    // on close
                });
            }
        },
        activated(){
            if (this.name)
            {
                this.getReward()
            }
        },

    }
</script>

<style scoped>
   @import '../../static/css/index.css';
</style>



