<template>
    <div class="appBox">
        <div class="signin">
            <img src="@/static/images/login/caifushukaiqi_logo.png" alt class="logo" />
            <p class="title">MillionCoin</p>
            <ul class="list">
                <li>
                    <img src="@/static/images/login/login_user_icon.png" alt="" />
                    <input type="text" v-model="loginForm.username" placeholder="请输入用户名" />
                </li>
                <li>
                    <img src="@/static/images/login/login_password_icon.png" alt="" />
                    <input type="password"  v-model="loginForm.password" placeholder="请输入密码" />
                </li>
            </ul>
            <div class="login_box"><button @click="login">登录</button></div>
            <div v-if="redirect !== 'rsuccess'" class="fast_reg"><router-link to="/register">快速注册</router-link></div>
            <div v-if="redirect == 'rsuccess'" @click="goback" class="fast_reg_back">返回上一步备份账号和密码</div>
        </div>
    </div>
</template>

<script>
    import { getLogin, setLogin, removeLogin } from '@/utils/auth'
    import { login } from '@/api/user'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    export default {
        name: "Login",
        data() {
            return {
                // loginForm:{
                //     username:'2FEZ7BBYKB4',
                //     password:'123456'
                // } ,
                loginForm:{
                    username:'',
                    password:''
                } ,
                redirect:undefined,
            }
        },
        watch: {
            $route: {
                handler: function(route) {
                    this.redirect = route.query && route.query.redirect
                },
                immediate: true
            }
        },
        mounted()
        {
            this.getUser()
        },
        methods:{

            //获取用户登录信息
            getUser()
            {
               let info = getLogin()
               info = info.split('=')
                this.loginForm.username = info[0]
                this.loginForm.password = info[1]
            },

            login()
            {
                if (this.loginForm.username.length == 0)
                {
                    Toast('请填写用户名');
                    return false;
                }
                if (this.loginForm.password.length == 0)
                {
                    Toast('请填写密码');
                    return false;
                }

                this.$store.dispatch('user/login', this.loginForm).then(() => {

                    /** 保存一下账号密码 */
                    let setInfo = this.loginForm.username+'='+this.loginForm.password
                    setLogin(setInfo);
                    this.$router.push({ path: '/' })

                    // if (this.redirect == 'rsuccess')
                    // {
                    //     this.$router.push({ path: '/' })
                    //
                    // } else
                    // {
                    //     this.$router.push({ path: this.redirect || '/' })
                    // }


                }).catch(() => {

                })
            },

            // 返回备份账号密码
            goback()
            {
                this.$router.go(-1)
            }
        }

    }
</script>

<style scoped>
     @import '../../static/css/login.css';
</style>



