<template>
    <div class="signin">
        <img src="@/static/images/login/caifushukaiqi_logo.png" alt class="logo" />
        <p class="title">MillionCoin</p>
        <p class="code">邀请码</p>
        <ul class="list">
            <li>
                <img src="@/static/images/login/login_yaoqing_icon.png" alt />
                <input v-model="inviteCode" :readonly="readState" type="text" placeholder="请输入邀请码" />
            </li>
        </ul>
        <div class="reg_box"><button @click="register">立即注册</button></div>
    </div>
</template>

<script>
    import { register } from "../../api/user";
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    export default {
        name: "Register",
        data() {
            return {
                inviteCode: '',
                readState: false
            }
        },
        mounted()
        {

            this.inviteCode = this.$route.query.code ? this.$route.query.code : ''
            this.readState = this.inviteCode ? true : false
        },
        methods: {

            // 注册
           register()
           {
               if (this.inviteCode.length == 0 || this.inviteCode == '')
               {
                   Toast('请填写邀请码')
               }
               register({inviteCode:this.inviteCode}).then(res => {

                   this.$router.push({
                       path: 'rsuccess',
                       query: {
                           username: res.username,
                           password: res.password,
                       }
                   })
               })
           }
        }
    }
</script>

<style scoped>
     @import "../../static/css/register.css";
</style>



