import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/index/index'
import Login from '@/page/login/index'
import Mine from '@/page/Mine/index'
import Register from '@/page/Register/index'
import RSuccess from '@/page/Register/success'
import Wcurrency from '@/page/Mine/wallet/wcurrency'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Index',
            meta:{keepAlive: true},
            component: () => import('@/page/index/index')

        },
        {
            path: '/adDetail',
            name: 'AdDetail',
            component: () => import('@/page/index/adDetail')
        },
        {
            path: '/cooperation',
            name: 'Cooperation',
            component: () => import('@/page/index/cooperation')
        },
        {
            path: '/currency',
            name: 'Currency',
            component: () => import('@/page/index/currency')
        },
        {
            path: '/login',
            name: 'Login',
            component: Login
        },
        {
            path: '/register',
            name: 'Register',
            component: Register
        },
        {
            path: '/rsuccess',
            name: 'RSuccess',
            component: RSuccess
        },
        {
            path: '/mine',
            name: 'Mine',
            meta:{keepAlive: true},
            component: () => import('@/page/Mine/index'),
        },
        {
            path: '/introduction',
            name: 'Introduction',
            meta:{keepAlive: true},
            component: () => import('@/page/Mine/introduction'),
        },
        {
            path: '/wcurrency',
            name: 'Wcurrency',
            component: () => import('@/page/Mine/wallet/wcurrency')
        },
        {
            path: '/recharge',
            name: 'Recharge',
            component: () => import('@/page/Mine/wallet/recharge')
        },
        {
            path: '/rechargelog',
            name: 'Rechargelog',
            component: () => import('@/page/Mine/wallet/rechargelog')
        },
        {
            path: '/withdraw',
            name: 'Withdraw',
            component: () => import('@/page/Mine/wallet/withdraw')
        },
        {
            path: '/redindex',
            name: 'Redindex',
            component: () => import('@/page/Mine/Red/index')
        },
        {
            path: '/invite',
            name: 'Invite',
            component: () => import('@/page/Mine/invite')
        },
        {
            path: '/backups',
            name: 'Backups',
            component: () => import('@/page/Mine/backups')
        },
        {
            path: '/opinion',
            name: 'Opinion',
            component: () => import('@/page/Mine/opinion')
        },
        {
            path: '/userinfo',
            name: 'Userinfo',
            component: () => import('@/page/Mine/userinfo')
        },
        {
            path: '/nickname',
            name: 'Nickname',
            component: () => import('@/page/Mine/Nickname')
        },
        {
            path: '/ruleNotice',
            name: 'RuleNotice',
            component: () => import('@/page/Mine/RuleNotice/index')
        },
        {
            path: '/mrule',
            name: 'Mrule',
            component: () => import('@/page/Mine/RuleNotice/rule')
        },
        {
            path: '/mnotice',
            name: 'Mnotice',
            component: () => import('@/page/Mine/RuleNotice/notice')
        },
        {
            path: '/noticeInfo',
            name: 'NoticeInfo',
            component: () => import('@/page/Mine/RuleNotice/noticeInfo')
        },
        {
            path: '/wechatcode',
            name: 'Wechatcode',
            component: () => import('@/page/Mine/wechatcode')
        },
        {
            path: '/tree',
            name: 'treeindex',
            meta:{keepAlive: true},
            component: () => import('@/page/tree/index')
        },
        {
            path: '/treestrategy',
            name: 'Treestrategy',
            component: () => import('@/page/tree/treestrategy')
        },
        {
            path: '/treefinance',
            name: 'Treefinance',
            component: () => import('@/page/tree/finance')
        },
        {
            path: '/treeinfo',
            name: 'Treeinfo',
            component: () => import('@/page/tree/info')
        },
        {
            path: '/treeinvite',
            name: 'Treeinvite',
            component: () => import('@/page/tree/invite')
        },
        {
            path: '/treeteam',
            name: 'Treeteam',
            component: () => import('@/page/tree/team')
        },
        {
            path: '/treeupgrade',
            name: 'Treeupgrade',
            component: () => import('@/page/tree/upgrade')
        },
        {
            path: '/jointree',
            name: 'Jointree',
            component: () => import('@/page/index/jointree')
        },
        {
            path: '/treecreate',
            name: 'Treecreate',
            component: () => import('@/page/tree/create')
        },
        {
            path: '/redgroup',
            name: 'Redgroup',
            meta:{keepAlive: true},
            component: () => import('@/page/red/redgroup')
        },
        {
            path: '/redcreate',
            name: 'Redcreate',
            component: () => import('@/page/red/create')
        },
        {
            path: '/redstrategy',
            name: 'Redstrategy',
            component: () => import('@/page/red/redstrategy')
        },
        {
            path: '/redchat',
            name: 'Redchat',
            component: () => import('@/page/red/chat')
        },
        {
            path: '/redshare',
            name: 'Redshare',
            component: () => import('@/page/red/share')
        },
        {
            path: '/reddetail',
            name: 'Reddetail',
            component: () => import('@/page/red/detail')
        },
        {
            path: '/usergroup',
            name: 'Usergroup',
            component: () => import('@/page/red/usergroup')
        },
        {
            path: '/redpacketlog',
            name: 'Redpacketlog',
            component: () => import('@/page/red/redpacketlog')
        },
        {
            path: '/groupnotice',
            name: 'Rroupnotice',
            component: () => import('@/page/red/groupnotice')
        },
        {
            path: '/grouprule',
            name: 'Rrouprule',
            component: () => import('@/page/red/grouprule')
        },
        {
            path: '/searchred',
            name: 'Searchred',
            component: () => import('@/page/index/searchred')
        },
        {
            path: '/currencyred',
            name: 'Currencyred',
            component: () => import('@/page/index/currencyred')
        },
        {
            path: '/hotred',
            name: 'Hotred',
            component: () => import('@/page/index/hotred')
        },
        {
            path: '/treelog',
            name: 'Treelog',
            component: () => import('@/page/mine/tree/treelog')
        }

    ]
})



