<template>

    <!--  底部tab  -->
    <div class="tabBar">
        <ul>
            <router-link to="/" tag="li" class="tabHome"><i></i><p>首页</p></router-link>
            <router-link to="/redgroup" tag="li" class="tabHb"><i></i><p>红包群</p></router-link>
            <router-link to="" tag="li" class="tabAdd"><i @click="showbar = !showbar" ></i></router-link>
            <router-link to="/tree" tag="li" class="tabCash"><i></i><p>财富树</p></router-link>
            <router-link to="/mine" tag="li" class="tabMy"><i></i><p>我的</p></router-link>
        </ul>
        <transition
                style="z-index: 99"
                name="custom-classes-transition"
                enter-active-class="animated slideInUp"
                leave-active-class="animated fadeOutDown"
        >
            <p class="showHide" v-show="showbar">
                <router-link to="/redcreate"><img src="@/static/images/tab/found_image_hongbao.png" alt=""><span>创建红包群</span></router-link>
                <router-link to="/treecreate"><img src="@/static/images/tab/found_image_tree.png" alt=""><span>创建财富树</span></router-link>
            </p>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "TabBar",
        data()
        {
            return {
                showbar:false
            }
        },
        activated(){
            this.showbar = false;
        }
    }
</script>

<style scoped>
    @import '../../static/css/tabbar.css';
    .tabBar .showHide {
        text-align: center;
        width: 100%;
        background: #fff;
        position: fixed;
        bottom: 0.48rem;
        left: 0;
        z-index: 99;
        display: block;
        box-shadow: 0 -6px 10px 0 rgba(0, 0, 0, 0.1);
        border-radius: 0.12rem 0.12rem 0 0;
    }
    .tabBar .showHide a {
        display: block;
        padding: 0.14rem 0;
        border-bottom: 0.01rem solid #f5f5f5;
        font-size: 0.15rem;
        color: #131313;
    }
    .tabBar .showHide a span {
        vertical-align: middle;
    }
    .tabBar .showHide a img {
        width: 0.3rem;
        height: 0.3rem;
        margin-right: 0.14rem;
        vertical-align: middle;
    }
</style>



