import request from '@/utils/request'

// 获取所有币种
export function currencyList(keyword) {
    return request({
        url: '/index/currencyList',
        method: 'get',
        params: {keyword}
    })
}
// 获取第一个币种
export function currencyFirst() {
    return request({
        url: '/index/currencyFirst',
        method: 'get',
    })
}
// 获取一个币种信息
export function currencyInfo(currency_id) {
    return request({
        url: '/index/currencyInfo',
        method: 'get',
        params: {currency_id}
    })
}

// 获取一个币种信息
export function homeList(currency_id) {
    return request({
        url: '/index/homeList',
        method: 'get',
        params: {currency_id}
    })
}

// 搜索红包
export function searchRed(room_number) {
    return request({
        url: '/index/searchRed',
        method: 'get',
        params: {room_number}
    })
}

// 币种区红包
export function currencyRed(params) {
    return request({
        url: '/index/currencyRed',
        method: 'get',
        params
    })
}
// 热门币种红包
export function hotRed(params) {
    return request({
        url: '/index/hotRed',
        method: 'get',
        params
    })
}
// 广告信息
export function adList(params) {
    return request({
        url: '/index/adList',
        method: 'get',
        params
    })
}
// 广告详情
export function adDetail(params) {
    return request({
        url: '/index/adDetail',
        method: 'get',
        params
    })
}

// 奖励
export function reward(params) {
    return request({
        url: '/index/reward',
        method: 'get',
        params
    })
}

// 项目方
export function cooperation(data) {
    return request({
        url: '/index/cooperation',
        method: 'post',
        data
    })
}




